.about-programm {
  background-image: url("../img/back_second.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
  //url("../img/circles_top.png"),
}
.about-programm__wrap {
  padding-top: 30px;
  padding-left: 100px;
  width: 100%;
}
.about-programm__logo {
  width: 15%;
  height: 15%;
}

.about-programm__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 48px;
  font-weight: 700;
  text-align: center;
  color: $text-btn;
  margin-bottom: 100px;
}

.about-programm__link {
  display: block;
  align-items: center;
  margin: 0 auto;
  width: 290px;
  height: 65px;
  text-decoration: none;
  color: $text-btn;
  font-family: "Open Sans", "Calibri", sans-serif;
  font-size: 24px;
  line-height: 50px;
  font-weight: 700;
  background-color: $main-col;
  text-align: center;
  text-transform: uppercase;
  padding-top: 5px;
  margin-top: 35px;
  border-radius: 10px;
}
