.header {
  background-image: url("../img/backFirst.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  padding-bottom: 30px;
}

.header__wrap {
  padding-top: 30px;
  padding-left: 100px;
  width: 100%;
}

.header__logo {
  width: 15%;
  height: 15%;
}

.header__title-wrap {
  margin: 0 auto;
  text-align: center;
  width: 50%;
}

.header__title {
  font-size: 2.5em;
  font-weight: 700;
  margin-bottom: 25px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 48px;
  font-weight: 700;

}
.header__text {
  font-size: 0.9em;
  text-align: center;
}

.header__link {
  display: block;
  align-items: center;
  margin: 0 auto;
  width: 290px;
  height: 65px;
  text-decoration: none;
  color: $text-btn;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 24px;
  line-height: 50px;
  font-weight: 700;
  background-color: $main-col;
  text-align: center;
  text-transform: uppercase;
  padding-top: 5px;
  margin-top: 35px;
  border-radius: 10px;
}








