.trainers {
  margin-top: 264px;
  display: flex;
  justify-content: space-around;
  position: relative;
  padding-bottom: 45px;
  &::after {
    content: "";
    position: absolute;
    background-image: url("../img/circles_bot.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}

.trainers__title {
  background-color: $main-col;
  color: $global-col;
  font-size: 66px;
  line-height: 65px;
  font-family: "Open Sans", "Calibri", sans-serif;
  letter-spacing: -2%;
  width: 44%;
  text-align: left;
  margin-left: 167px;
  padding-left: 60px;
  padding-top:  158px;
  padding-bottom: 157px;
}
.trainers__text {
  background-color: rgba(201, 216, 220, 0.9);
  font-family: "Open Sans", "Calibri", sans-serif;
  font-size: 24px;
  line-height: 33px;
  letter-spacing: -2%;
  width: 43%;
  max-height: 355px;
  text-align: left;
  margin-right: 167px;
  margin-left: -155px;
  margin-top: 45px;
  padding-left: 38px;
  padding-right: 38px;
  padding-top: 58px;
}

.trainers__text-head {
  font-size: 36px;
}
