.programm-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  font-size: 0.9em;
  margin-top: 20px;
}

.programm-list__elem {
  font-family: "Open Sans", "Calibri", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  width: 38%;
  position: relative;
  margin-top: 30px;
  &--finance {
    padding-left: 225px;
  }
  &--desing-thinking {
    padding-right: 225px;
  }
  &--system-thinking{
    margin: 0 auto;
    padding-left: 131px;
    padding-right: 131px;
  }
  &::after {
    content: "";
    position: absolute;
    width: 100px;
    height: 100px;
    top: 2px;
    left: 41%;
    background-color: $main-col;
    border-radius: 80px;
    background-position: center;
    background-repeat: no-repeat;
  }
  &--ileader::after {
    background-image: url("../img/iLeader.svg");
    background-size: 43px 43px;

  }
  &--team::after {
    background-image: url("../img/team.svg");
    background-size: 45px 50px;

  }
  &--finance::after {
    left: 61%;
    background-image: url("../img/finance.svg");
    background-size: 43px 43px;

  }
  &--desing-thinking::after {
    left: 22%;
    background-image: url("../img/design_thinking.svg");
    background-size: 43px 43px;

  }
  &--system-thinking::after {
    background-image: url("../img/system_thinking.svg");
    background-size: 43px 43px;

  }
}

.programm-list__logo {
  display: inline-block;
  width: 50%;
  height: 50%;
  margin: auto;
  vertical-align: middle;

}
.programm-list__text {
  text-align: center;
  margin-top: 105px;
}
