.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  font-family: "Open Sans", "Calibri", sans-serif;
  color: $text-btn;
  padding: 0 10%;
}
.list__elem {
  width: 366px;
  background-color: rgba(45, 41, 41, 0.5);
  margin-right: 16px;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;

  &--desing-think {
    margin-left: auto;
    margin-right: 30px;
  }
  &--leader {
    margin-right: auto;
  }
}
.list__title {
  font-weight: 700;
  font-size: 24px;
  padding-top: 20px;
  padding-left: 30px;
  padding-right: 70px;
  text-align: left;
  letter-spacing: -2px;
}
.list__text {
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 18px;
  padding-left: 30px;
  padding-right: 30px;
  text-align: left;
  letter-spacing: -1px;
}
.list__wrap {
  margin-top: auto;
  cursor: pointer;
  &:hover {
    display: none;
  }
  /*&:hover + .list__popup {
    display: block;//

  }*/

  &--team {
    background-color: rgba(40, 199, 250, 0.5);
    &:hover .list__popup {
      //background-color: black;
      //display: block;
    }//
  }
  &--finance {
    background-color: rgba(119, 90, 218, 0.5);
  }
  &--system-think {
    background-color: rgba(0, 38, 81, 0.5);
  }
  &--desing-think {
    background-color: rgba(255, 48, 79, 0.5);
  }
  &--leader {
    background-color: rgba(33, 230, 193, 0.5);
  }

}

.list__logo {
  margin-left: 30px;

}

.list__popup {
  display: none;
  padding: 12px 30px;
  font-size: 18px;
  letter-spacing: -1px;
}
